html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

ul {
  list-style: none;
}

button,
input,
select,
textarea {
  margin: 0;
}

html {
  box-sizing: border-box;
}

* {
  box-sizing: inherit;
}

*:before, *:after {
  box-sizing: inherit;
}

img,
embed,
object,
audio,
video {
  height: auto;
  max-width: 100%;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
  text-align: left;
}

@font-face {
  font-family: DinPro;
  src: url(../fonts/DINPro.otf);
}

html,
body {
  font-family: DinPro, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
}

body {
  display: flex;
  flex-flow: column nowrap;
  min-height: 100vh;
  justify-content: space-between;
}

header {
  flex: 0;
}

main {
  flex: 1;
}

footer {
  flex: 0;
}

header {
  background-color: #333333;
}

main {
  background-color: #d8d5d0;
}

.top-panel {
  display: flex;
  justify-content: space-between;
  font-family: DinPro, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  height: 34px;
  line-height: 34px;
  padding: 0 13px;
  background-color: #000;
  color: #fbea5e;
  font-size: 23.8px;
  text-shadow: 1px 1px 1px rgba(251, 234, 94, 0.6);
  text-transform: uppercase;
}

.top-panel .left, .top-panel .right {
  flex: 1;
}

.top-panel .right {
  text-align: right;
}

.top-panel .center {
  flex: 1;
  display: flex;
  justify-content: inherit;
}

.top-panel .center .jackpot-item {
  display: flex;
}

.top-panel .center .jackpot-item .jackpot-name {
  margin-right: 11px;
}

.top-panel .center .jackpot-item .jackpot-counter {
  font-size: 19px;
  position: relative;
}

.top-panel .center .jackpot-item .jackpot-counter .row {
  display: flex;
  overflow: hidden;
  background-image: linear-gradient(top, rgba(200, 200, 200, 0.2) 0%, rgba(200, 200, 200, 0.2) 40%, black 45%, black 55%, black 100%);
  background-color: #222222;
  border-radius: 3px;
}

.top-panel .center .jackpot-item .jackpot-counter .row .jackpot-counter-cell {
  width: 22.1px;
  height: 34px;
  position: relative;
}

.top-panel .center .jackpot-item .jackpot-counter .row .jackpot-counter-cell + .top-panel .center .jackpot-item .jackpot-counter .row .jackpot-counter-cell {
  box-shadow: inset 0.09em 0 0.1em 0px rgba(255, 255, 255, 0.2);
}

.top-panel .center .jackpot-item .jackpot-counter .row .jackpot-counter-cell .roller {
  margin: 0 auto;
  height: 34px;
  width: 20px;
  transform-style: preserve-3d;
  transition: transform 1000ms ease-in-out;
  animation: spin 10000ms infinite linear;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  will-change: transform;
  transform: rotate3d(1, 0, 0, -12deg);
}

.top-panel .center .jackpot-item .jackpot-counter .row .jackpot-counter-cell .roller .plane {
  position: absolute;
  height: 34px;
  width: 20px;
  text-align: center;
  line-height: 1;
  color: #fbea5e;
  backface-visibility: hidden;
}

.jackpot-counter .figure0 {
  transform: rotate3d(1, 0, 0, 0deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure1 {
  transform: rotate3d(1, 0, 0, 36deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure2 {
  transform: rotate3d(1, 0, 0, 72deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure3 {
  transform: rotate3d(1, 0, 0, 108deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure4 {
  transform: rotate3d(1, 0, 0, 144deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure5 {
  transform: rotate3d(1, 0, 0, 180deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure6 {
  transform: rotate3d(1, 0, 0, 216deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure7 {
  transform: rotate3d(1, 0, 0, 252deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure8 {
  transform: rotate3d(1, 0, 0, 288deg) translate3d(0, 0, 31px);
}

.jackpot-counter .figure9 {
  transform: rotate3d(1, 0, 0, 324deg) translate3d(0, 0, 31px);
}

@keyframes spin {
  0% {
    transform: rotate3d(1, 0, 0, -12deg);
  }
  10% {
    transform: rotate3d(1, 0, 0, -48deg);
  }
  20% {
    transform: rotate3d(1, 0, 0, -84deg);
  }
  30% {
    transform: rotate3d(1, 0, 0, -120deg);
  }
  40% {
    transform: rotate3d(1, 0, 0, -156deg);
  }
  50% {
    transform: rotate3d(1, 0, 0, -192deg);
  }
  60% {
    transform: rotate3d(1, 0, 0, -228deg);
  }
  70% {
    transform: rotate3d(1, 0, 0, -264deg);
  }
  80% {
    transform: rotate3d(1, 0, 0, -300deg);
  }
  90% {
    transform: rotate3d(1, 0, 0, -336deg);
  }
  100% {
    transform: rotate3d(1, 0, 0, -372deg);
  }
}
